import {request} from '../../util/request';
import { API_BASE_URL, ACCESS_TOKEN } from '../../constants';

export function save_user(user) {
    if(!localStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return request({
        url: API_BASE_URL + "/securityusers/users",
        method: 'POST',
        body: JSON.stringify(user)
    });
}

export function list_users(username,size,page) {
    if(!localStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return request({
        url: API_BASE_URL + "/securityusers/users/?username="+username+"&size="+size+"&page="+page,
        method: 'GET'
    });
}

export function list_roles(name) {
    if(!localStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return request({
        url: API_BASE_URL + "/securityroles/roles?name="+name,
        method: 'GET'
    });
}


