import React, {Component} from 'react';
import {
    Progress,
    Modal,
    Button,
    notification,
    Table,
    Divider,
    Tag,
    Popconfirm,
    Row,
    Col,
    Input, Switch, List,
    Form, Icon, InputNumber, Checkbox, DatePicker, TimePicker, Select
} from 'antd';

import {saveVisitaActiva, getImagesProduct} from '../util/APIUtils';

const FormItem = Form.Item;
const Option = Select.Option;
const {TextArea} = Input;

class AddProduct extends Component {

    constructor(props) {
        super(props);
        this.state = {
            name: "", category: "", count: 0, price: 0, description: "", disponible: false,
            newImg: "",
            imagenes: []
        }


        this.changeName = this.changeName.bind(this);
        this.changeCategory = this.changeCategory.bind(this);
        this.changeCount = this.changeCount.bind(this);
        this.changePrice = this.changePrice.bind(this);
        this.changeDescripction = this.changeDescripction.bind(this);
        this.changeDisponible = this.changeDisponible.bind(this);
        this.changeUrlImg = this.changeUrlImg.bind(this);
        this.addImage = this.addImage.bind(this);
        this.saveProduct = this.saveProduct.bind(this);

    }

    changeName = (event) => this.setState({name: event.target.value});
    changeCategory = (event) => this.setState({category: event.target.value});
    changeCount = (event) => this.setState({count: event.target.value});
    changePrice = (event) => this.setState({price: event.target.value});
    changeDescripction = (event) => this.setState({description: event.target.value});
    changeDisponible = (checked) => this.setState({disponible: checked});
    changeUrlImg = (event) => this.setState({newImg: event.target.value});

    addImage = () => {
        let _imagenes = this.state.imagenes;
        _imagenes.push(this.state.newImg);
        this.setState({
            imagenes: _imagenes,
            newImg: ""
        });
    }
    volver = () => {
        this.props.history.push({pathname: "/visitaactiva/list", state: {size: 'state'}});
    }
    cleanValues = () => this.setState({
        name: "",
        category: "",
        count: 0,
        price: 0,
        description: "",
        disponible: false,
        imagenes: [],
        visible: true,
    });

    componentDidMount() {
        const state = this.props.location.state;
        if (state != null) {
            this.setState({
                idPublic: state.idPublic,
                name: state.name,
                category: state.category,
                count: state.count,
                price: state.price,
                description: state.description,
                disponible: state.disponible,
            });

            getImagesProduct(state.idPublic).then(response => {
                this.setState({
                    imagenes: response.content
                })
            });
        }
    }


    saveProduct = () => {

        let product = {
            idPublic: this.state.idPublic,
            name: this.state.name,
            category: this.state.category,
            count: this.state.count,
            price: this.state.price,
            description: this.state.description,
            disponible: this.state.disponible,
            imagenes: this.state.imagenes
        };


        saveVisitaActiva(product).then(response => {

            notification.success({
                message: 'VisitaActiva App',
                description: "The info have been successfully saved.",
            });
            this.props.history.push({
                pathname: "/visitaActiva/list",
                state: {
                    size: 'state'
                }
            });
            this.cleanValues();
        }).catch(error => {

            if (error.status === 401) {
                this.props.handleLogout('/login', 'error', 'You have been logged out. Please login to vote');
            } else {
                notification.error({
                    message: 'Polling App',
                    description: error.message || 'Sorry! Something went wrong. Please try again!'
                });
            }
        });

    }

    render() {


        return (
            <div className="container" style={{marginTop: 50}}>
                <Form>

                    <Row type="flex" justify="start">
                        <Col span={10}>
                            <FormItem label="Nombre Producto">
                                <Input size="large" name="name" placeholder="Nombre Producto"
                                       value={this.state.name}
                                       onChange={(event) => this.changeName(event)}/>
                            </FormItem>
                        </Col>
                        <Col span={3}></Col>
                        <Col span={10}>
                            <FormItem label="Categoria">
                                <Input size="large" name="category" placeholder="Categoria"
                                       value={this.state.category}
                                       onChange={(event) => this.changeCategory(event)}/>
                            </FormItem>
                        </Col>
                    </Row>
                    <Row type="flex" justify="start">
                        <Col span={10}>
                            <FormItem label="Cantidad">
                                <Input size="large" name="count" placeholder="Cantidad"
                                       value={this.state.count}
                                       onChange={(event) => this.changeCount(event)}/>
                            </FormItem>
                        </Col>
                        <Col span={3}></Col>
                        <Col span={10}>
                            <FormItem label="Precio">
                                <Input size="large" name="price" placeholder="Precio"
                                       value={this.state.price}
                                       onChange={(event) => this.changePrice(event)}/>
                            </FormItem>
                        </Col>
                    </Row>
                    <FormItem label="Descripcion">
                        <TextArea size="large" name="description" placeholder="Descripcion"
                                  value={this.state.description}
                                  onChange={(event) => this.changeDescripction(event)}
                                  autosize={{minRows: 4, maxRows: 6}}/>
                    </FormItem>
                    <FormItem label="Disponible">
                        <Switch defaultChecked name="disponible"
                                onChange={this.changeDisponible}/>
                    </FormItem>
                    <FormItem label="Nueva Imagen">
                        <Input size="large" name="newImg" placeholder="Nueva Imagen" value={this.state.newImg}
                               onChange={(event) => this.changeUrlImg(event)}/>
                        <Button onClick={() => this.addImage()}>Agregar</Button>

                        <List
                            size="large"
                            header={<div>Listado de Imagenes</div>}
                            bordered
                            dataSource={this.state.imagenes}
                            renderItem={item => <List.Item>{item}</List.Item>}
                        />

                    </FormItem>

                    <br/>
                    <Button onClick={() => this.volver()}>Cancelar</Button>
                    <Button onClick={() => this.saveProduct()}>Guardar Product</Button>

                </Form>

                <br/>
                <br/>
                <br/>
                <br/>
            </div>
        );
    }

}

export default AddProduct;



