import React, {Component} from 'react';
import {
    Progress,
    Modal,
    Button,
    notification,
    Table,
    Divider,
    Tag,
    Popconfirm,
    Row,
    Col,
    Input, Switch, List,
    Form, Icon, InputNumber, Checkbox, DatePicker, TimePicker, Select
} from 'antd';

import {saveVisitaActiva, getAllVisitaActiva, signup} from '../util/APIUtils';

const FormItem = Form.Item;
const Option = Select.Option;
const {TextArea} = Input;

class Productlist extends Component {

    constructor(props) {
        super(props);
        this.state = {
            products: [],
            pagination: {},
            loading: false,
            visible: false,
            confirmLoading: false,
            tipos: [
                {id: 1, value: "Test 1"}, {id: 2, value: "Test 2"}, {id: 3, value: "Test 3"}
            ],
            name: "", category: "", count: 0, price: 0, description: "", disponible: false,
            newImg: "",
            imagenes: []
        }

        this.columns = [
            {
                title: 'Nombre Producto',
                dataIndex: 'name',
                key: 'name',
                sorter: true
            },
            {
                title: 'Categoria',
                dataIndex: 'category',
                key: 'category',
                sorter: true
            },
            {
                title: 'Cantidad',
                dataIndex: 'count',
                key: 'count',
                sorter: true
            },
            {
                title: 'Precio',
                dataIndex: 'price',
                key: 'price',
                sorter: true
            },
            {
                title: 'Disponible',
                dataIndex: 'disponibleView',
                key: 'disponibleView',
                sorter: true
            }, {
                title: "Acciones",
                key: "actions",
                render: (text, record) => (
                    <Button onClick={() => this.editarProduct(record)}>Editar</Button>
                )
            }

        ];
        this.editarProduct = this.editarProduct.bind(this);

    }


    handleTableChange = (pagination, filters, sorter) => {
        const pager = {...this.state.pagination};
        pager.current = pagination.current;
        this.setState({
            pagination: pager
        });

        var field = "";
        var sort = "";
        if (sorter.order == "descend") {
            sort = "desc";
        } else {
            sort = "asc";
        }
        if (sorter.field === undefined) {
            field = "id";
        } else {
            field = sorter.field;
        }
        this.loadData("?size=" + pagination.pageSize + "&page=" + pagination.current + "&sort=" + field + "," + sort);
    };

    loadData(params) {

        // console.log("params:", params);
        this.setState({loading: true});

        getAllVisitaActiva({}, params).then(response => {
            const pagination = {...this.state.pagination};
            pagination.total = response.content.totalElements;

            this.setState({
                loading: false,
                data: response.content.content,
                pagination
            });
        }).catch(error => {
            if (error.status === 401) {
                this.props.handleLogout('/login', 'error', 'You have been logged out. Please login to vote');
            } else {
                notification.error({
                    message: 'Polling App',
                    description: error.message || 'Sorry! Something went wrong. Please try again!'
                });
            }
        });

    };

    componentDidMount() {
        this.loadData("?size=10&page=0");
    }


    editarProduct = (product) => {
        this.props.history.push({
            pathname: "/visitaactiva/add", state: {
                idPublic: product.idPublic,
                name: product.name,
                category: product.category,
                count: product.count,
                price: product.price,
                description: product.description,
                disponible: product.disponible,
                imagenes: product.imagenes != null ? product.imagenes : [],
                visible: true,
            }
        });

    }
    //Eventos Modal
    addProductView = () => {
        this.props.history.push({pathname: "/visitaactiva/add"});
    }

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visible: false,
        });
    }

    render() {


        return (
            <div className="container" style={{marginTop: 50}}>
                <Button type="button" type="primary" onClick={this.addProductView}>Agregar nuevo Producto</Button>


                <Table
                    rowKey="Id"
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    loading={this.state.loading}
                    dataSource={this.state.data}
                    columns={this.columns}/>
                <br/>
                <br/>
                <br/>
                <br/>
            </div>
        );
    }

}

export default Productlist;



