import {
    Table,
    Input,
    InputNumber,
    notification,
    Form
} from 'antd';
import React, {
    Component
} from 'react';
import {
    list_users,
    list_roles
} from '../security/service/SecurityService';

const FormItem = Form.Item;

const columns = [{
    title: 'Id',
    dataIndex: 'id',
    sorter: true,
    key: 'id',
}, {
    title: 'User Name',
    dataIndex: 'username',
    sorter: true,
    key: 'username',
}, {
    title: 'Email',
    dataIndex: 'email',
    sorter: true,
    key: 'email',
}];

class AdminSysUsers extends Component {

    state = {
        data: [],
        pagination: {},
        loading: false,
        size: 10,
        page: 0
    };

    handleTableChange = (pagination, filters, sorter) => {
        const pager = { ...this.state.pagination };
        pager.current = pagination.current;
        this.setState({
            pagination: pager,
            size: pagination.pageSize,
            page: pagination.current
        });
        this.fetch();
    }
    fetch() {
        list_users("", this.state.size, this.state.page)
            .then(response => {
                const pagination = { ...this.state.pagination };
                pagination.total = response.totalElements;
                this.setState({
                    loading: false,
                    data: response.content,
                    pagination
                });
            }).catch(error => {
                if (error.status === 401) {
                    this.props.handleLogout('/login', 'error', 'You have been logged out. Please login to vote');
                } else {
                    notification.error({
                        message: 'Polling App',
                        description: error.message || 'Sorry! Something went wrong. Please try again!'
                    });
                }
            });
    }
    componentDidMount() {
        this.fetch();
    }
    render() {
        return (
            <div>
                <Table
                    columns={columns}
                    rowKey={record => record.id}
                    dataSource={this.state.data}
                    pagination={this.state.pagination}
                    loading={this.state.loading}
                    onChange={this.handleTableChange}
                />
            </div>
        );
    }
}

export default AdminSysUsers;


